<template>
  <div v-click-away="closeSideBar">
    <div class="relative">
      <div class="h-screen relative z-10 bg-gray-50 shadow w-24 transition-all duration-100 ease-in overflow-hidden" :class="{'w-80': sidebarIsOpen}">
        <div class="px-3 my-8 flex justify-between items-center">
          <img src="../../assets/iivlogo.png" class="w-16 h-16 rounded-full" alt="logo" />
          <p class="px-2 text-center text-primary text-md">O'zbekiston Resbublikasi Ichki Ishlar Vazirligi</p>
        </div>
        <ul class="px-[3px]">
          <li v-for="link in sidebarLinks[store.getters['getUserInfo'].role]" :key="link.title">
            <template v-if="Array.isArray(link.path)">
              <Dropdown
                dropdown-title-class="flex items-center justify-between flex-wrap gap-[11px] cursor-pointer p-2 text-gray-500 border-l-[3px] border-[transparent]"
                :title="link.title"
                :close-on-click-outside="false"
              >
                <template #title-content>
                  <img class="w-[32px] h-[32px]" :src="sidebarIcons[link.iconName]" alt="" />
                </template>
                <template #dropdown-content>
                  <router-link v-for="path in link.path" :key="path.path" :to="path.path" class="sidebar-item">
                    <p class="ml-2">
                      {{ path.title }}
                    </p>
                  </router-link>
                </template>
              </Dropdown>
            </template>
            <template v-else>
              <router-link :to="link.path" class="sidebar-item">
                <img class="w-[32px] h-[32px]" :src="sidebarIcons[link.iconName]" alt="" />
                <p>
                  {{ link.title }}
                </p>
              </router-link>
            </template>
          </li>
        </ul>
      </div>
      <button v-if="isSidebarOpen" class="absolute -right-9 top-3 text-white bg-primary shadow-md p-1 py-2" @click="setSidebarStatus(false)">
        <Icon icon="mdi:chevron-left" width="30" height="30" />
      </button>
    </div>
  </div>
</template>

<script setup>
import sidebarLinks from '../../utils/sidebarLinks';

import Boshsaxifa from '../../assets/Boshsaxifa.svg';
import Aniqlash from '../../assets/Bolanianiqlash.svg';
import Bandliknianiqlash from '../../assets/Bandliknianiqlash.svg';
import Taqsimlash from '../../assets/Taqsimlash.svg';
import Monitoring from '../../assets/Monitoring.svg';
import Qabulqilish from '../../assets/markazgaqabulqilish.svg';
import Hisobotlar from '../../assets/Hisobotlar.svg';
import Bolalar from '../../assets/Bollar.svg';
import Markazyaratish from '../../assets/Markazyaratish.svg';
import Xodimqoshish from '../../assets/Hodimqoshish.svg';
import {computed, onMounted, onUnmounted, ref} from 'vue';
import {useStore} from 'vuex';
import {Icon} from '@iconify/vue';
import Dropdown from '../Common/Dropdown';

const sidebarIcons = {
  Boshsaxifa,
  Aniqlash,
  Bandliknianiqlash,
  Taqsimlash,
  Monitoring,
  Qabulqilish,
  Hisobotlar,
  Bolalar,
  Markazyaratish,
  Xodimqoshish,
};

const sidebarIsOpen = ref(true);
const store = useStore();

function setSidebarStatus(status) {
  store.commit('setSidebarStatus', status);
}

const dropdownRef = ref();

onMounted(() => {
  console.log(dropdownRef.value);
});

function watchWidowResize() {
  let windowWidth = document.documentElement.clientWidth;
  if (windowWidth >= 1280 && isSidebarOpen.value) {
    setSidebarStatus(false);
  }
}

onMounted(() => {
  window.addEventListener('resize', watchWidowResize);
});
onUnmounted(() => {
  window.removeEventListener('resize', watchWidowResize);
});

const isSidebarOpen = computed(() => store.getters['getIsSidebarOpened']);

function closeSideBar() {
  if (isSidebarOpen.value) {
    setSidebarStatus(false);
  }
}
</script>

<style scoped>
.router-link-active {
  @apply bg-[#F0F5FF] !text-[#4785FE] transition-all duration-100 border-l-[3px] !border-[#4785FE];
}
.router-link-active img {
  filter: invert(44%) sepia(49%) saturate(3254%) hue-rotate(206deg) brightness(104%) contrast(99%) !important;
}
.sidebar-item {
  @apply flex items-center flex-wrap gap-[11px] cursor-pointer p-2 text-gray-500 border-l-[3px] border-[transparent];
}
</style>
