<template>
  <div class="bg-gray-50 shadow-md flex justify-between items-center w-full h-16 text-black none-print">
    <button v-if="!isSidebarOpen" class="ml-3 block xl:hidden hover:text-primary cursor-pointer text-gray-600" @click.stop.prevent="setSidebarStatus(true)">
      <Icon icon="mdi:menu" width="30" height="30" />
    </button>
    <p class="px-5">
      {{ $store.getters['getUserInfo'].full_name_position }}
    </p>
    <div class="flex items-center gap-[12px] justify-end px-3">
      <DonationButton v-if="notMonitoringRole" :totalAmount="totalAmount" :donationsList="donationsList" />
      <NotificationButton v-if="notMonitoringRole" :notificationsList="notificationsList" />
      <Devider />
      <ProfileDropdown />
    </div>
  </div>
</template>

<script setup>
import Devider from '../../Common/Devider';
import ProfileDropdown from './ProfileDropdown';
import {Icon} from '@iconify/vue';
import {computed, watch} from 'vue';
import {useStore} from 'vuex';
import NotificationButton from '../../Notification/NotificationButton';
import DonationButton from '../../Donation/DonationButton';
import useNotificationsWebsocket from '../../../composables/useNotificationsWebsocket';
import {useRoute} from 'vue-router';
const {notificationsList, donationsList, totalAmount, getDonationTotalAmount, getDonationsList} = useNotificationsWebsocket(true);

const store = useStore();
const route = useRoute();

const MONITORING = 'Monitoring';

const notMonitoringRole = computed(() => store.getters['getUserInfo'].role !== MONITORING);

function setSidebarStatus(status) {
  store.commit('setSidebarStatus', status);
}

watch(
  () => route.fullPath,
  (val) => {
    getDonationTotalAmount();
    getDonationsList();
  }
);

const isSidebarOpen = computed(() => store.getters['getIsSidebarOpened']);
</script>

<style scoped></style>
