<script setup>
import {computed, defineProps, toRefs} from "vue";

const props = defineProps({
  status:Number,
  label:String
})
const { label,status } = toRefs(props)

const STATUSES = {
  RECIEVED:1,
  CANCELLED:3,
  APPROVED:2,
}

const badgeClass = computed(()=>{
  if(status.value === STATUSES.RECIEVED){
    return 'bg-[#98D7E4] text-[#0D3B44]'
  }else if(status.value === STATUSES.CANCELLED){
    return 'bg-[#FB4C2F] text-[#FFFFFF]'
  }else if(status.value === STATUSES.APPROVED){
    return 'bg-[#B3EFD3] text-[#0B4F30]'
  }else{
    return  ''
  }
})
console.log(badgeClass.value)

</script>
<template>
  <div class="rounded-[4px] text-[12px] px-[4px] py-[2px]" :class="badgeClass">
   {{label}}
  </div>
</template>


