<template>
  <Dropdown
      :with-arrow="false"
      dropdown-title-class="flex justify-between items-center cursor-pointer"
      dropdown-wrapper-class="origin-top-right absolute right-0 rounded-md shadow-lg bg-white py-1 z-20"
  >
    <template #title-content>
      <img
          :src="$store.getters['getUserInfo'].photo ? `http://192.168.1.146:8080/${$store.getters['getUserInfo'].photo}` : logoUser"
          class="rounded-full object-cover w-12 h-12"
          alt="user-logo"
      >
    </template>
    <template #dropdown-content>
      <button class=" cursor-pointer text-gray-700 block px-4 py-2 text-sm flex items-center w-full" tabindex="-1" @click="logout">
        <Icon class="mr-2" icon="mdi:logout" width="24" height="24" />
        Chiqish
      </button>
      <button class="cursor-pointer text-gray-700 block px-4 py-2 text-sm flex items-center w-full" tabindex="-1" @click.stop="logger">
        <Icon icon="mdi:at" class="mr-2" width="24" height="24" />
        {{ $store.getters["getUserInfo"].username }}
      </button>
    </template>
  </Dropdown>
</template>

<script>
import logoUser from "../../../assets/img/logoUser.png";
import axios from "../../../services/api/axios";
import {Icon} from '@iconify/vue';
import Dropdown from "../../Common/Dropdown";

export default {
  name: "ProfileDropdown",
  components: {
    Dropdown,
    Icon
  },
  data() {
    return {
      isOpen: false,
      logoUser
    };
  },

  methods: {
    logger(){
      console.log('dsds')
    },
    toggleDrop() {
      this.isOpen = !this.isOpen;
    },
    closeDrop() {
      this.isOpen = false;
    },
    async logout() {
      await axios.get("/auth/logout");
      this.$store.dispatch('setAuth',false)
      await this.$router.push("/auth");
    }
  }
};
</script>

