import {onMounted, ref} from 'vue';
import NotificationsService from '../services/notifications.service';
import DonationService from '../services/donation.service';

export default function useNotificationsWebsocket(first) {
  const notificationsList = ref([]);
  const donationsList = ref([]);
  const totalAmount = ref(null);
  async function getNotificationsList() {
    try {
      const resp = await NotificationsService.getNotificationsList();
      notificationsList.value = resp.data;
    } catch (e) {
      console.log({e});
    }
  }
  async function getDonationsList() {
    try {
      const resp = await DonationService.getDonationsNotificationList();
      donationsList.value = resp.data;
    } catch (e) {
      console.log({e});
    }
  }
  async function getDonationTotalAmount() {
    try {
      const resp = await DonationService.getTotalDonations();
      totalAmount.value = resp.data;
    } catch (e) {
      console.log({e});
    }
  }
  async function connect() {
    // let baseURL = '';
    // if (process.env.NODE_ENV === 'production') {
    //   baseURL = window.location.hostname + ':' + window.location.port;
    // } else {
    //   // baseURL = `evoya.technocorp.uz`;
    //   // baseURL = `192.168.43.16:8000`;
    // }
    await getNotificationsList();
    await getDonationsList();
    await getDonationTotalAmount();
    setInterval(async () => {
      await getNotificationsList();
      await getDonationsList();
      await getDonationTotalAmount();
    }, 15000);
    // const chatSocket = new WebSocket(`ws://${baseURL}/ws/socket-server/`);
    // chatSocket.onmessage = async (e) => {
    //   console.log(e.data);
    //   const message = JSON.parse(e.data);
    //   await getNotificationsList();
    //   await getDonationsList();
    //   await getDonationTotalAmount();
    //   if (message.type === 'notification_changed') {
    //     await getNotificationsList();
    //   } else if (message.type.includes('donation')) {
    //     await getDonationsList();
    //     await getDonationTotalAmount();
    //   }
    // };
    // let socket = new SockJS(`ws://evoya.technocorp.uz/ws/socket-server/`);
  }

  onMounted(async () => {
    first && (await connect());
  });
  return {
    notificationsList,
    donationsList,
    totalAmount,
    getDonationsList,
    getDonationTotalAmount,
    connect,
  };
}
