  <router-link to="/all-donations" class="flex items-center font-inter gap-[6px] bg-[#F5F5F5] px-[12px] py-[10px] select-none rounded-6px">
    <span class="text-[#AFAFAF] text-[16px]">Xayriya:</span>
    <CurrencyInput v-model="amount" class="cursor-pointer text-[#34C38F] text-[18px]" :options="options" />
  </router-link>
</template>

<script setup>
import Dropdown from '../Common/Dropdown';
import {ref, watch, defineProps} from 'vue';
import CurrencyInput from '../CurrencyInput';
import Donations from './Donations';
import useDonations from '../../composables/useDonations';
import useAlert from '../../composables/useAlert';
import {useRoute, useRouter} from 'vue-router';
import Badge from '../Common/Badge.vue';
const {getDonationComplete} = useDonations();
const {alertIt} = useAlert();

const router = useRouter();
const route = useRoute();

const props = defineProps(['totalAmount', 'donationsList']);

const amount = ref();

watch(
  () => props.totalAmount,
  () => {
    amount.value = parseInt(props.totalAmount.total_amount);
  }
);

const completeDonation = async (id) => {
  const resp = await getDonationComplete(id);
  alertIt(resp.message);
  router.push({name: route.name, query: {id: id}});
};

const options = {
  currency: 'UZS',
  currencyDisplay: 'symbol',
  hideCurrencySymbolOnFocus: false,
  hideGroupingSeparatorOnFocus: false,
  hideNegligibleDecimalDigitsOnFocus: false,
  autoDecimalDigits: false,
  useGrouping: true,
  accountingSign: false,
};
</script>

