<template>
  <div v-click-away="closeDrop" class="relative">
    <div class="" :class="dropdownTitleClass" @click.stop="toggleDropdown">
      <div class="flex items-center gap-[11px]">
        <slot name="title-content"></slot>
        {{ title }}
      </div>
      <div v-if="withArrow" class="text-gray-700 w-[24px] h-[18px] float-right">
        <Icon v-if="isOpen" class="w-full h-full" icon="mdi:chevron-up" />
        <Icon v-else class="w-full h-full" icon="mdi:chevron-down" />
      </div>
    </div>
    <div v-if="isOpen" class="" :class="dropdownWrapperClass">
      <slot name="dropdown-content"></slot>
    </div>
  </div>
</template>

<script setup>
import {defineProps, ref, toRefs} from 'vue';
import {Icon} from '@iconify/vue';

const isOpen = ref(false);

const toggleDropdown = () => (isOpen.value = !isOpen.value);
const closeDrop = () => {
  if (closeOnClickOutside.value) {
    isOpen.value = false;
  }
};

const props = defineProps({
  title: {
    type: String,
    default: '',
    required: false,
  },
  dropdownTitleClass: {
    type: String,
    default: '',
    required: false,
  },
  dropdownWrapperClass: {
    type: String,
    default: '',
    required: false,
  },
  closeOnClickOutside: {
    type: Boolean,
    default: true,
    required: false,
  },
  withArrow: {
    type: Boolean,
    default: true,
    required: false,
  },
});
const {title, dropdownTitleClass, dropdownWrapperClass, withArrow, closeOnClickOutside} = toRefs(props);
</script>

<style scoped></style>
